
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Icon from 'vux/src/components/icon/index.vue'

import {httpUrl} from '../../http_url';

export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: '退出登录'
},
showMenus: false,
sm_status:'',
bank_status:'',
people_status:'',
pengyuan_status:'',
liveface_status:'',
sp_status:'',
ht_status:'',
orderid:'',
ht_url:'',
isIOS:true,
transfinite:''      

}
},
created(){
this.getData();
},
components: {
XHeader,
PopupHeader,
Actionsheet,
Group,
Cell,
Grid,
GridItem,
Icon
},
methods:{
getData(){
let orderid = this.$route.query.orderid;
const _this = this;
let postData = this.$qs.stringify({
orderid :orderid
})
this.$axios({
method: 'post',
url:httpUrl.guideInfo,
data:postData
}).then((res)=>{
console.log(res.data)
let data = res.data.retData;
if(res.data.errCode==200){
this.sm_status = data.sm_status;
this.bank_status = data.bank_status;
this.people_status = data.people_status;
this.ht_status = data.ht_status;
this.pengyuan_status = data.pengyuan_status;
this.liveface_status = data.liveface_status;
this.sp_status = data.sp_status;
this.ht_url = data.ht_url;
this.transfinite = data.transfinite;
this.orderid = data.orderid;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goBankCertify(){
if(this.sm_status == 0){
this.$vux.toast.show({
type:'warn',
text: "请先完成实名认证！"
})
}else {
this.$router.push({
path:'/bank_certify',
query: {
step_type: '1',
orderid:this.orderid
}
});
}
},
goInformationEdit(){
if(this.bank_status == 0){
this.$vux.toast.show({
type:'warn',
text: "请先完成银行卡认证！"
})
}else {
this.$router.push({
path:'/information_edit',
query: {
step_type: '1',
orderid:this.orderid
}
});
}
},
goLiveFace(){
let _this = this;
if(this.people_status == 0){
this.$vux.toast.show({
type:'warn',
text: "请先完成联系人认证！"
})
}else {
if(this.transfinite == 1){
this.$vux.alert.show({
title: '错误提示',
content: '失败次数过多，请联系客服：0755-22211333',
onHide () {
_this.$router.push('/home');
}
})
}else {
this.$router.push({
path:'/qiniu_upload',
query: {
step_type: '1',
orderid:this.orderid
}
});
}
}
},
goSignature(){
let _this = this;
if(this.liveface_status == 0){
this.$vux.toast.show({
type:'warn',
text: "请先完成人脸识别认证！"
})
}else if(this.liveface_status == 2 ) {
if(this.transfinite == 1){
this.$vux.alert.show({
title: '错误提示',
content: '人脸识别失败次数过多，请联系客服：0755-22211333',
onHide () {
_this.$router.push('/home');
}
})
}else {
this.$vux.toast.show({
type:'warn',
text: "请先完成人脸识别认证！"
})
}
}else {
this.$router.push({
path:'/signature',
query: {
step_type: '1',
orderid:this.orderid
}
});
}
},
openHt(){
window.open(this.ht_url);
},
goHome(){
this.$router.push('/home');
}
}
}



